/**
 * DatePicker.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import DatePicker from '../DatePicker/DatePicker';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

/**
 * Custom Form Dropdown class
 */
class DateRangePicker extends Component {
  render() {
    const {
      values,
      onChange,
      desc,
      extraInfo,
      startName,
      endName,
      startDate,
      endDate,
      errorStringStart,
      errorStringEnd,
      offsetDays,
    } = this.props;

    const errorString = (errorStringStart || errorStringEnd) && `${`${errorStringStart} ` || ''}${errorStringEnd || ''}`;

    return (
      <FormInputWrapper
        desc={desc}
        extraInfo={extraInfo}
        errorString={errorString}
        isDateRange
      >
        <div className="date-range-picker-new">
          <DatePicker
            startValue={values && values[startName] && values[startName].value}
            endValue={values && values[endName] && values[endName].value}
            onChange={onChange}
            name={startName}
            endName={endName}
            isRange
            startDate={startDate}
            endDate={endDate}
            errorString={errorString}
            offsetDays={offsetDays}
          />
        </div>
      </FormInputWrapper>
    );
  }
}

DateRangePicker.defaultProps = {
  desc: null,
  extraInfo: null,
  startDate: null,
  endDate: null,
  errorStringStart: null,
  errorStringEnd: null,
  values: null,
  offsetDays: 0,
};

DateRangePicker.propTypes = {
  onChange: PropTypes.func.isRequired,
  startName: PropTypes.string.isRequired,
  endName: PropTypes.string.isRequired,
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  startDate: PropTypes.instanceOf(Date),
  endDate: PropTypes.instanceOf(Date),
  errorStringStart: PropTypes.string,
  errorStringEnd: PropTypes.string,
  values: PropTypes.object,
  offsetDays: PropTypes.number,
};

export default DateRangePicker;
