/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import People from 'assets/images/EditForm/People.svg';
import Arrow from '../../../assets/images/forms/arrow.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './Persons.scss';

/**
 * Custom Form Dropdown class
 */
class Persons extends Component {
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // isOpened - Whether dropdown is opened
    // listItems - The actual dropdown list
    // selectedString - The selected item string
    // height - Control height
    // numItemsDisplay - How many items to display
    // maxheight - Max height of the list
    // listHeight - Height of the list. (Items count + control)
    this.state = {
      isOpened: null,
    };

    this.setError = this.setError.bind(this);
    this.clearError = this.clearError.bind(this);
    this.handleSelectClick = this.handleSelectClick.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);

    this.handleValue = this.handleValue.bind(this);
  }

  componentDidMount() {
    // Add Handler to handle click outside of screen
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  setError(message) {
    this.formParent.setErrorString(message);
  }

  clearError() {
    this.formParent.clearErrorString();
  }

  /**
   * End selection if click on outside
   *
   * @param {object} event
   */
  handleClickOutside(event) {
    const { isOpened } = this.state;

    // If clicked outside, go to exit
    if (
      isOpened &&
      this.wrapperRef &&
      this.wrapperRefControl &&
      (!this.wrapperRef.contains(event.target) && !this.wrapperRefControl.contains(event.target))
    ) {
      // Close it
      this.setState({
        isOpened: false,
      });
    }
  }

  handleSelectClick() {
    const { isOpened } = this.state;
    this.setState({ isOpened: !isOpened });
  }

  async handleValue(event, name, isAdd) {
    event.preventDefault();
    const {
      nameAdult,
      nameChildren,
      namePlanType,
      nameTripType,
      values,
      onChange,
    } = this.props;

    const oldNumAdult = (values[nameAdult] && values[nameAdult].value) || 0;
    const oldNumChildren = (values[nameChildren] && values[nameChildren].value) || 0;

    let numAdult = oldNumAdult;
    let numChildren = oldNumChildren;
    const isSingleTrip = values[nameTripType] && values[nameTripType].value === 'Single Trip';

    const minAdult = 1;
    const maxAdult = isSingleTrip ? 20 : 2;
    const minChildren = 0;
    const maxChildren = 8;

    if (name === nameAdult) {
      numAdult = Math.min(Math.max(minAdult, numAdult + (isAdd ? 1 : -1)), maxAdult);
    } else {
      numChildren = Math.min(Math.max(minChildren, numChildren + (isAdd ? 1 : -1)), maxChildren);
    }

    if (numChildren > 0) {
      numAdult = Math.min(Math.max(1, numAdult), 2);
    }

    if (oldNumAdult !== numAdult) {
      await onChange('number', nameAdult, numAdult);
    }
    if (oldNumChildren !== numChildren) {
      await onChange('number', nameChildren, numChildren);
    }

    const planType = values && values[namePlanType] && values[namePlanType].value;

    if (numChildren > 0) {
      if (planType !== 'Family') {
        await onChange('text', namePlanType, 'Family');
      }
    } else if (numAdult > 1) {
      if (planType !== 'Group') {
        await onChange('text', namePlanType, 'Group');
      }
    } else if (planType !== 'Individual') {
      await onChange('text', namePlanType, 'Individual');
    }

    const tripType = values && values[nameTripType] && values[nameTripType].value;
    if (numAdult > 1 && numChildren === 0) {
      if (tripType !== 'Single Trip') {
        await onChange('selection', nameTripType, 'Single Trip');
      }
    }
  }

  render() {
    const { isOpened } = this.state;

    const {
      desc,
      extraInfo,
      errorString,
      nameAdult,
      nameChildren,
      values,
      isPrex,
    } = this.props;

    const errorStyle = errorString ? 'error' : '';
    const arrowStyle = isOpened ? 'angle-up' : 'angle-down';
    let openedStyle = isOpened ? 'drop-container-opened' : 'drop-container-closed';
    openedStyle = isOpened === null ? 'drop-container-closed-init' : openedStyle;

    const numAdult = (values[nameAdult] && values[nameAdult].value) || 0;
    const numChildren = (values[nameChildren] && values[nameChildren].value) || 0;

    const zIndexStyle = isOpened ? 'drop-open' : 'drop-close';

    return (
      <FormInputWrapper
        desc={desc}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div className="position-relative">
          <div
            className={`persons-container ${errorStyle} ${zIndexStyle}`}
            ref={node => this.wrapperRefControl = node}
          >
            <div className="img-container">
              <img
                src={People}
                alt="People.svg"
              />
            </div>
            <div
              className="text-input"
              onClick={() => this.handleSelectClick()}
              onKeyPress={() => {}}
              role="button"
              tabIndex={-1}
            >
              <p>{numAdult} adult, {numChildren} children</p>
            </div>
            <div
              className="drop-arrow"
              onClick={() => this.handleSelectClick()}
              onKeyPress={() => {}}
              role="button"
              tabIndex={-1}
            >
              <img
                src={Arrow}
                alt="arrow.svg"
                className={arrowStyle}
              />
            </div>
            <div
              className={`drop-container ${openedStyle}`}
              ref={node => this.wrapperRef = node}
            >
              <div className="value-container">
                <button onClick={e => this.handleValue(e, nameAdult, false)}><div className="minus fa fa-minus" /></button>
                <p>
                  <span className="count">{numAdult}</span>
                  <span className="desc">adults</span>
                </p>
                <button onClick={e => this.handleValue(e, nameAdult, true)}><div className="plus fa fa-plus" /></button>
              </div>
              <div className="value-container">
                {!isPrex && <button onClick={e => this.handleValue(e, nameChildren, false)}><div className="minus fa fa-minus" /></button>}
                <p>
                  <span className="count">{numChildren}</span>
                  <span className="desc">children</span>
                </p>
                {!isPrex && <button onClick={e => this.handleValue(e, nameChildren, true)}><div className="plus fa fa-plus" /></button>}
              </div>
            </div>
          </div>
        </div>
      </FormInputWrapper>
    );
  }
}

Persons.defaultProps = {
  desc: null,
  extraInfo: null,
  errorString: null,
  values: {},
  isPrex: false,
};


Persons.propTypes = {
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  nameAdult: PropTypes.string.isRequired,
  nameChildren: PropTypes.string.isRequired,
  namePlanType: PropTypes.string.isRequired,
  nameTripType: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  values: PropTypes.object,
  isPrex: PropTypes.bool,
};

export default Persons;
