/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Input } from 'reactstrap';
import MagnifyGlass from 'assets/images/forms/magnifyGlass.svg';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './TextInput.scss';

/**
 * Custom Form Dropdown class
 */
class TextInput extends Component {
  render() {
    const {
      name,
      desc,
      placeholderString,
      extraInfo,
      errorString,
      value,
      onChange,
      handlePostCode,
      rows,
      noInternalDesc,
      type,
      toUpper,
      readOnly,
    } = this.props;

    // eslint-disable-next-line
    const inputType = type === 'password' ? 'password' : type === 'textarea' ? 'textarea' : type === 'number' ? 'number' : '';

    const errorStyle = errorString ? 'error' : '';
    const titleStyle = value ? 'focused-title' : '';
    const textareaStyle = inputType === 'textarea' ? 'textarea-padding' : '';
    const inputStyle = inputType === 'textarea' ? '' : 'absolute-input';
    const noInternalDescStyle = noInternalDesc ? 'no-internal-desc' : '';
    const readOnlyStyle = readOnly ? 'read-only' : '';

    return (
      <FormInputWrapper
        desc={desc}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div className="position-relative">
          <div
            className={`text-input-container-v3 ${textareaStyle} ${errorStyle} ${readOnlyStyle}`}
          >
            {
              !noInternalDesc &&
              <div className={`title-header ${titleStyle}`}>
                {placeholderString}
              </div>
            }
            <div className={['text-input', noInternalDescStyle, inputStyle].join(' ')}>
              <Input
                className="text"
                type={inputType}
                placeholder={(noInternalDesc && placeholderString) || ''}
                name={name}
                value={value || ''}
                onChange={e => onChange(type, name, e.target.value && toUpper ? e.target.value.toUpperCase() : e.target.value)}
                rows={rows}
                disabled={readOnly}
              />
            </div>
            {
              type === 'postal' && handlePostCode ?
                <button
                  type="button"
                  className="postcode"
                  onClick={handlePostCode}
                >
                  <img src={MagnifyGlass} alt="magGlass.svg" />
                </button>
              :
                null
            }
          </div>
        </div>
      </FormInputWrapper>
    );
  }
}

TextInput.defaultProps = {
  value: null,
  desc: null,
  extraInfo: null,
  errorString: null,
  handlePostCode: null,
  rows: 4,
  noInternalDesc: false,
  toUpper: false,
  readOnly: false,
};


TextInput.propTypes = {
  placeholderString: PropTypes.string.isRequired,
  value: PropTypes.string,
  name: PropTypes.string.isRequired,
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  handlePostCode: PropTypes.func,
  rows: PropTypes.number,
  noInternalDesc: PropTypes.bool,
  type: PropTypes.string.isRequired,
  toUpper: PropTypes.bool,
  readOnly: PropTypes.bool,
};

export default TextInput;
