/**
 * DateInner.js
 * DateView only for rendering
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import PropTypes from 'prop-types';
import React, { Component, Fragment } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import moment from 'moment';

import CloseIcon from 'assets/images/common/CloseIcon.svg';
import Arrow from 'assets/images/forms/arrow.svg';
import Triangle from 'assets/images/forms/triangle.svg';

import MonthYearSelector from '../MonthYearSelector/MonthYearSelector';
import DaysList from '../DaysList/DaysList';
import Calender from '../Calender/Calender';

import './DateInner.scss';

const SINGLE = 0;
const MULTIPLELEFT = 1;
const MULTIPLERIGHT = 2;

/**
 * Custom Form Dropdown class
 */
class DateInner extends Component {
  constructor(props) {
    super(props);

    this.state = {
      hoverDate: null,
      dateRangeEndOverwrite: null,
    };

    this.setCalender = this.setCalender.bind(this);
    this.setDateRangeEndOverwrite = this.setDateRangeEndOverwrite.bind(this);
  }

  // componentDidUpdate(prevProps) {
  //   const { startValue } = this.props;

  //   if (!isDateEqual(startValue, prevProps.startValue)) {
  //     this.setDateRangeEndOverwrite(startValue);
  //   }
  // }

  setDateRangeEndOverwrite(value) {
    const {
      dateRangeEnd,
      offsetDays,
    } = this.props;

    let newEnd = value;

    if (dateRangeEnd) {
      const maxEnd = moment(dateRangeEnd).add(offsetDays, 'days').toDate();
      newEnd = newEnd > maxEnd ? maxEnd : newEnd;
    }

    this.setState({ dateRangeEndOverwrite: newEnd });
  }

  /**
   * setCalender
   *
   * Set visible calender
   *
   * @param {number} month
   * @param {number} year
   */
  setCalender(month, year) {
    const {
      dateRangeEndOverwrite,
    } = this.state;

    const {
      dateRangeStart,
      dateRangeEnd,
      setNewDisplayDate,
    } = this.props;

    let date = new Date(year, month);

    // Cap date range
    if (date < dateRangeStart) {
      date = dateRangeStart;
    } else if (date > (dateRangeEndOverwrite || dateRangeEnd)) {
      date = dateRangeEndOverwrite && dateRangeEndOverwrite > dateRangeEnd ? dateRangeEndOverwrite : dateRangeEnd;
    }

    setNewDisplayDate(date);
  }

  renderMonthSelectorArrows(isBack, month, year) {
    const { isRange, isResp } = this.props;

    return (
      <Col
        xs={1}
        className="date-arrow-style"
      >
        <button
          type="button"
          onClick={() => {
            // eslint-disable-next-line
            this.setCalender(isBack ? month - 1 : (isRange && !isResp ? month : month + 1), year);
          }}
        >
          <img
            src={Arrow}
            alt="arrow.svg"
            className={isBack ? 'angle-left' : 'angle-right'}
          />
        </button>
      </Col>
    );
  }

  /**
   * renderDateInner
   *
   * Render the popup od the datepicker
   */
  renderDateInner(type) {
    const { dateRangeEndOverwrite, hoverDate } = this.state;

    const {
      currDisplayDate,
      isRange,
      setSelectedDate,
      startValue,
      endValue,
      dateRangeStart,
      dateRangeEnd,
      isSelectingStartDate,
      isResp,
      offsetDays,
    } = this.props;

    const dateToUse = new Date(currDisplayDate.getFullYear(), currDisplayDate.getMonth(), 1);
    if (type === MULTIPLERIGHT) {
      dateToUse.setMonth(dateToUse.getMonth() + 1);
    }

    const month = dateToUse.getMonth();
    const year = dateToUse.getFullYear();

    let upperStyle = '';
    if (isRange && !isResp) {
      upperStyle = type === MULTIPLERIGHT ? 'range-right' : 'range-left';
    }

    return (
      <div className="date-inner-container">
        {
          type !== MULTIPLERIGHT ?
            <div
              role="button"
              className="close-button-container"
              onKeyPress={() => {}}
              onClick={() => {
                setSelectedDate(startValue, true);
                setSelectedDate(endValue, false);
              }}
              tabIndex={-1}
            >
              <img src={CloseIcon} className="close-icon" alt="close" />
            </div>
            : null
        }
        <Row className={upperStyle} noGutters>
          {type !== MULTIPLERIGHT && this.renderMonthSelectorArrows(true, month, year)}
          <Col xs={6} className="selector-left">
            <MonthYearSelector
              setCalender={this.setCalender}
              currDisplayDate={dateToUse}
              dateRangeStart={dateRangeStart}
              dateRangeEnd={dateRangeEnd}
              isSingle={type === SINGLE}
            />
          </Col>
          <Col xs={4} className="selector-right">
            <MonthYearSelector
              isYear
              setCalender={this.setCalender}
              currDisplayDate={dateToUse}
              dateRangeStart={dateRangeStart}
              dateRangeEnd={dateRangeEnd}
              isSingle={type === SINGLE}
            />
          </Col>
          {type !== MULTIPLELEFT && this.renderMonthSelectorArrows(false, month, year)}
        </Row>
        <DaysList />
        <Calender
          isRange={isRange}
          startValue={startValue}
          endValue={endValue}
          dateRangeStart={dateRangeStart}
          dateRangeEnd={dateRangeEnd}
          dateRangeEndOverwrite={dateRangeEndOverwrite}
          currDisplayDate={dateToUse}
          hoverDate={hoverDate}
          setSelectedDate={setSelectedDate}
          isSelectingStartDate={isSelectingStartDate}
          setDateRangeEndOverwrite={this.setDateRangeEndOverwrite}
          setHoverDate={value => this.setState({ hoverDate: value })}
          offsetDays={offsetDays}
        />
      </div>
    );
  }

  render() {
    const {
      isOpened,
      isRange,
      isArrowUp,
      inputWidth,
      isSelectingStartDate,
      isResp,
    } = this.props;

    const openedStyle = isOpened ? 'dateinner-opened' : '';
    const dateOpenStyle = isOpened ? 'date-container-opened' : 'date-container-closed';


    const respStyle = isResp ? 'inner-absolute' : '';
    const isArrowUpToUse = isResp || isArrowUp;
    const arrowStyle = isArrowUpToUse ? 'arrow-up' : 'arrow-down';
    const rangeBorders = isRange ? '' : '';

    let transform = `translateX(20px) ${isArrowUpToUse ? '' : 'rotate(180deg)'}`;
    if (isRange && inputWidth !== null) {
      transform = `translateX(${(isSelectingStartDate ? 0 : inputWidth * 0.5) + 20}px) ${isArrowUpToUse ? '' : 'rotate(180deg)'}`;
    }

    return (
      <Fragment>
        <div className={`${openedStyle} ${respStyle} dateinner-v3`}>
          <div className={dateOpenStyle}>
            <img
              className={['arrow-range', arrowStyle, respStyle].join(' ')}
              src={Triangle}
              alt="triangle.svg"
              style={{ transform }}
            />
            <div
              className={`list-container list-opened ${rangeBorders}`}
              ref={node => this.listCont = node}
            >
              {this.renderDateInner(isRange && !isResp ? MULTIPLELEFT : SINGLE)}
              {isRange && !isResp && this.renderDateInner(MULTIPLERIGHT)}
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

DateInner.defaultProps = {
  startValue: null,
  endValue: null,
  inputWidth: 0,
  offsetDays: 0,
};

DateInner.propTypes = {
  isOpened: PropTypes.bool.isRequired,
  isRange: PropTypes.bool.isRequired,
  currDisplayDate: PropTypes.instanceOf(Date).isRequired,
  startValue: PropTypes.instanceOf(Date),
  endValue: PropTypes.instanceOf(Date),
  dateRangeStart: PropTypes.instanceOf(Date).isRequired,
  dateRangeEnd: PropTypes.instanceOf(Date).isRequired,
  setSelectedDate: PropTypes.func.isRequired,
  setNewDisplayDate: PropTypes.func.isRequired,
  isArrowUp: PropTypes.bool.isRequired,
  isSelectingStartDate: PropTypes.bool.isRequired,
  inputWidth: PropTypes.number,
  isResp: PropTypes.bool.isRequired,
  offsetDays: PropTypes.number,
};

export default DateInner;
