/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Tooltip from 'components/Templates/Tooltip/Tooltip';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './ImageSelection.scss';

/**
 * Custom Form Dropdown class
 */
class ImageSelection extends Component {
  constructor(props) {
    super(props);

    this.state = {
      clickStyle: 'click-right',
    };
  }

  handleOnClick(value) {
    try {
      const {
        onChange,
        name,
        value: oldValue,
        inputData,
      } = this.props;

      const newValue = inputData.is_required === false && value === oldValue ? null : value;
      onChange(inputData.type, name, newValue);
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  renderTitle() {
    const { inputData } = this.props;

    if (!inputData.title && !inputData.tooltip) {
      return null;
    }

    try {
      return (
        <div className="title-container">
          <h3>{inputData.title}</h3>
          {
            inputData.tooltip &&
            <Tooltip
              id={inputData.slug}
            >
              <p>{inputData.tooltip}</p>
            </Tooltip>
          }
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderItem(itemData, index, isTooltip) {
    const { clickStyle } = this.state;
    const { inputData, value } = this.props;

    try {
      const isSelected = (inputData.is_required && !value && index === 0) || (value && value === itemData.value);
      const key = `${inputData.slug}-option-${index}`;

      return (
        <Col
          key={key}
          xs={6}
          className="item-container-wrapper"
        >
          <div className="item-container-wrapper-inner">
            <button
              type="button"
              className={`item-container ${isSelected && 'selected'} ${clickStyle}`}
              onClick={() => {
                this.setState({
                  clickStyle: index % 2 === 0 ? 'click-left' : 'click-right',
                }, () => this.handleOnClick(itemData.value));
              }}
            >
              <div className={`bg ${isSelected && 'bg-active'}`} />
              <div className="image">
                <img src={itemData.icon_img} alt={`${key}.svg`} />
              </div>
            </button>
            <div className="item-container-bottom">
              {
                isTooltip &&
                <div className="inner-tooltip">
                  {
                    itemData.tooltip &&
                    <Tooltip
                      id={key}
                    >
                      <p>{itemData.tooltip}</p>
                    </Tooltip>
                  }
                </div>
              }
              <div className={['desc', isTooltip ? 'desc-padding' : ''].join(' ')}>
                <p>{itemData.title}</p>
                <p className="sub-title">{itemData.sub_title}</p>
              </div>
            </div>
          </div>
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderOptions() {
    const { inputData } = this.props;

    try {
      const isTooltip = _.find(inputData.options, option => option && option.tooltip);

      return _.map(inputData.options, (option, index) => this.renderItem(option, index, isTooltip));
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    const { inputData, errorString, extraInfo } = this.props;

    if (!inputData) {
      return null;
    }

    return (
      <FormInputWrapper
        className="image-selection-margin"
        errorString={errorString}
        extraInfo={extraInfo}
      >
        <div className="image-selection-container">
          {this.renderTitle()}
          <Row className="options-container" noGutters>
            {this.renderOptions()}
          </Row>
        </div>
      </FormInputWrapper>
    );
  }
}

ImageSelection.defaultProps = {
  value: null,
  errorString: null,
  extraInfo: null,
};


ImageSelection.propTypes = {
  value: PropTypes.string,
  inputData: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  errorString: PropTypes.string,
  extraInfo: PropTypes.object,
};

export default ImageSelection;
