/**
 * SpeechScroll.js
 * The hovering dropdown that appears in the navbar and
 * the multi compare view insurer's row.
 *
 * It will auto close when out of the box.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Tooltip as TooltipStrap } from 'reactstrap';

import TooltipImg from 'assets/images/common/tooltip.svg';

import './Tooltip.scss';

class Tooltip extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: false,
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {

  }

  render() {
    const { isOpen } = this.state;
    const { id, innerClassName } = this.props;

    return (
      <div className="tooltip-container ">
        <TooltipStrap
          target={id}
          isOpen={isOpen}
          toggle={() => this.setState({ isOpen: false })}
          className="animated fade"
          innerClassName={[innerClassName, 'tooltip-inner-custom'].join(' ')}
          arrowClassName="tooltip-arrow-custom"
          placement="bottom-end"
        >
          {this.props.children}
        </TooltipStrap>
        <img
          id={id}
          src={TooltipImg}
          alt="Tooltip.svg"
          onMouseEnter={() => this.setState({ isOpen: true })}
        />
      </div>
    );
  }
}

Tooltip.defaultProps = {
  innerClassName: '',
};

Tooltip.propTypes = {
  id: PropTypes.string.isRequired,
  innerClassName: PropTypes.string,
};

export default Tooltip;
