/**
 * TravelDetail.js
 * Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'reactstrap';
import PropTypes from 'prop-types';

import TextInput from './TextInput/TextInput';
import Dropdown from './DropDown/DropDown';
import Checkbox from './Checkbox/Checkbox';
import DatePicker from './DatePicker/DatePicker';
import DateRangePicker from './DateRangePicker/DateRangePicker';
import ImageSelection from './ImageSelection/ImageSelection';
import AgeGroup from './AgeGroupV2/AgeGroup';
import Persons from './Persons/Persons';
import Selection from './SelectionNew/Selection';

class FieldGenerator extends Component {
  constructor(props) {
    super(props);

    this.state = {
      options: null,
    };

    this.getOptions = this.getOptions.bind(this);
  }

  componentDidMount() {
    this.getOptions();
    this.doPrefill();
  }

  componentDidUpdate(prevProps) {
    this.getOptions(prevProps);
    this.doPrefill(prevProps.errors);
  }

  getOptions(prevProps) {
    try {
      const { options } = this.state;
      const { constants, fieldData } = this.props;
      if (fieldData && fieldData.options_constant_key &&
        (!prevProps || !options || (fieldData.options_constant_key !== prevProps.fieldData.options_constant_key))) {
        this.setState({
          options: _.map(constants[fieldData.options_constant_key], v => ({ display_name: v, value: v })),
        });
      }
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }
  }

  doPrefill(prevErrors) {
    const {
      values,
      onChangeValue,
      fieldData,
      errors,
    } = this.props;

    try {
      const name = fieldData && fieldData.slug;
      if (values && values[name]) {
        // Set field type to values if contains prefill data
        if (!values[name].type) {
          onChangeValue(fieldData.type, name, values[name].value);
        }
      }

      // Set default selection for image selection
      if (
        fieldData.type === 'image_selection' &&
        (fieldData.is_required !== false) &&
        !values[name]
      ) {
        onChangeValue(fieldData.type, name, fieldData.options[0].value);
      }

      const error = errors && errors[name];
      const prevError = prevErrors && prevErrors[name];

      if (error && !prevError && this.node) {
        // window.scrollTo(0, this.node.getBoundingClientRect().y);
        window.scrollTo({
          top: this.node.getBoundingClientRect().y,
          left: 0,
          behavior: 'smooth',
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  renderInput() {
    const { options } = this.state;

    const {
      fieldData,
      values,
      onChangeValue,
      errors,
      noInternalDesc,
      extraInfo,
      handlePostCode,
      isPrex,
    } = this.props;

    const name = fieldData && fieldData.slug;
    const value = values && values[name] && values[name].value;
    const error = errors && errors[name];

    if (fieldData) {
      if (fieldData.type === 'padding') {
        return null;
      }

      const { conditions } = fieldData;

      let isRenderable = true;
      try {
        _.each(conditions || [], (condition, index) => {
          const isValid = values[condition.key].value === condition.value;
          if (index === 0) {
            isRenderable = isValid;
          } else {
            isRenderable = condition.type === 'or' ? isValid || isRenderable : isValid && isRenderable;
          }
        });
      } catch (e) {
        isRenderable = false;
      }

      if (!isRenderable) {
        return null;
      }

      switch (fieldData.type) {
        case 'email':
        case 'number':
        case 'time':
        case 'text':
        case 'textarea':
        case 'password':
        case 'postal': {
          return (
            <TextInput
              type={fieldData.type}
              value={value}
              onChange={onChangeValue}
              name={name}
              errorString={error}
              readOnly={fieldData.readOnly}
              placeholderString={fieldData.title}
              noInternalDesc={noInternalDesc}
              extraInfo={extraInfo}
              desc={fieldData.display_name}
              handlePostCode={handlePostCode}
            />
          );
        }
        case 'select':
        case 'multiple_select': {
          return (
            <Dropdown
              isMultiple={fieldData.type === 'multiple_select'}
              value={value}
              onChange={onChangeValue}
              name={name}
              errorString={error}
              placeholderString={fieldData.title}
              dropList={fieldData.options || options}
              noInternalDesc={noInternalDesc}
              extraInfo={extraInfo}
              desc={fieldData.display_name}
            />
          );
        }
        case 'checkbox': {
          return (
            <Checkbox
              value={value}
              name={name}
              desc={fieldData.title}
              onChange={onChangeValue}
              errorString={error}
              extraInfo={extraInfo}
            />
          );
        }
        case 'image_selection': {
          return (
            <ImageSelection
              value={value}
              onChange={onChangeValue}
              name={name}
              inputData={fieldData}
              errorString={error}
              extraInfo={extraInfo}
              desc={fieldData.display_name}
            />
          );
        }
        case 'age_group': {
          return (
            <AgeGroup
              inputData={fieldData}
              value={value}
              onChange={onChangeValue}
              name={name}
              errorString={error}
              extraInfo={extraInfo}
              desc={fieldData.display_name}
            />
          );
        }
        case 'date_range': {
          let startName = 'start_date';
          let endName = 'end_date';

          try {
            startName = fieldData.slugs && fieldData.slugs[0];
            endName = fieldData.slugs && fieldData.slugs[1];
          } catch (err) {
            // eslint-disable-next-line
          }

          return (
            <DateRangePicker
              values={values}
              onChange={onChangeValue}
              startName={startName}
              endName={endName}
              errorString={errors}
              startDate={fieldData.min_date}
              endDate={fieldData.max_date}
              errorStringStart={errors && errors[startName]}
              errorStringEnd={errors && errors[endName]}
              extraInfo={extraInfo}
              noInternalDesc={noInternalDesc}
              // desc={fieldData.display_name}
              offsetDays={fieldData.offset_days}
            />
          );
        }
        case 'date': {
          return (
            <DatePicker
              desc={fieldData.display_name}
              startValue={value}
              onChange={onChangeValue}
              name={name}
              placeholderString={fieldData.title}
              errorString={error}
              noInternalDesc={noInternalDesc}
              extraInfo={extraInfo}
              startDate={fieldData.min_date}
              endDate={fieldData.max_date}
            />
          );
        }
        case 'persons': {
          return (
            <Persons
              desc={fieldData.desc}
              values={values}
              onChange={onChangeValue}
              nameAdult="num_adult"
              nameChildren="num_children"
              namePlanType="plan_type"
              nameTripType="trip_type"
              errorString={error}
              extraInfo={extraInfo}
              isPrex={isPrex}
            />
          );
        }
        case 'button_select':
        case 'selection': {
          return (
            <Selection
              desc={fieldData.display_name}
              value={value}
              onChange={onChangeValue}
              name={name}
              options={fieldData.options}
              errorString={error}
              extraInfo={extraInfo}
              size={fieldData.tab_size}
            />
          );
        }
        default: return null;
      }
    }

    return null;
  }

  render() {
    const { fieldData } = this.props;

    if (!fieldData) {
      return null;
    }

    return (
      <Col xs={12} sm={fieldData.size || 12}>
        <div ref={node => this.node = node}>
          {this.renderInput()}
        </div>
      </Col>
    );
  }
}

FieldGenerator.defaultProps = {
  values: {},
  errors: {},
  noInternalDesc: false,
  extraInfo: null,
  constants: null,
  handlePostCode: null,
  isPrex: false,
};

FieldGenerator.propTypes = {
  values: PropTypes.object,
  errors: PropTypes.object,
  onChangeValue: PropTypes.func.isRequired,
  fieldData: PropTypes.object.isRequired,
  noInternalDesc: PropTypes.bool,
  extraInfo: PropTypes.object,
  constants: PropTypes.object,
  handlePostCode: PropTypes.func,
  isPrex: PropTypes.bool,
};

const mapStateToProps = state => ({
  constants: state.insurance.constants,
});

export default connect(mapStateToProps)(FieldGenerator);
