/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import AgeGroupItem from './AgeGroupItem/AgeGroupItem';

import './AgeGroup.scss';

/**
 * Custom Form Dropdown class
 */
class AgeGroup extends Component {
  constructor(props) {
    super(props);

    const { inputData, value } = this.props;
    let values = [];

    try {
      values = _.reduce(inputData.options, (result, v) => {
        const originalData = _.find(value, vo => vo.value === v.value);

        result.push({
          key: v.value,
          title: v.title,
          value: (originalData && originalData.count) || null,
        });
        return result;
      }, []);
    } catch (error) {
      // eslint-disable-next-line
    }

    this.state = {
      values,
      count: 0,
    };

    this.onChangeValue = this.onChangeValue.bind(this);

    this.getTotalCount = this.getTotalCount.bind(this);
    this.generateNewFormData = this.generateNewFormData.bind(this);
  }

  // Update form values on change
  onChangeValue(key, value) {
    const { values } = this.state;

    try {
      const newValue = [...values];
      const group = _.find(newValue, v => v.key === key);

      if (group) {
        group.value = value;
        this.setState({
          values: newValue,
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  getTotalCount() {
    const { values } = this.state;

    try {
      return _.reduce(values, (result, v) => {
        if (v.value > 0) {
          return result + v.value;
        }

        return result;
      }, 0);
    } catch (error) {
      // eslint-disable-next-line
    }

    return 0;
  }

  async generateNewFormData() {
    const { values } = this.state;
    const { onChange, name } = this.props;

    try {
      let count = 0;
      const newData = _.reduce(values, (result, v) => {
        if (v.value > 0) {
          count += v.value;
          result.push({
            value: v.key,
            count: v.value,
          });
        }

        return result;
      }, []);

      this.setState({
        count: this.getTotalCount(),
      });

      await onChange('age_group', name, newData);
      await onChange('number', 'no_of_employees', count);
    } catch (error) {
      // eslint-disable-next-line
    }

    return 0;
  }

  renderHeader() {
    const { count } = this.state;
    const { inputData } = this.props;

    try {
      return (
        <Row className="title-container" noGutters>
          <Col className="d-none d-md-flex" md={{ size: 4, order: 1 }} />
          <Col md={{ size: 4, order: 2 }} xs={{ size: 12, order: 2 }}>
            <h3>{inputData.title}</h3>
          </Col>
          <Col md={{ size: 4, order: 3 }} xs={{ size: 12, order: 4 }}>
            <p className="employees">{count} employees assigned</p>
          </Col>
          <Col md={{ size: 12, order: 4 }} xs={{ size: 12, order: 3 }}>
            <h6>{'Employees age for upcoming birthday\n(Current age + 1)'}</h6>
          </Col>
        </Row>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderAgeGroupItems() {
    const { values } = this.state;
    const { inputData } = this.props;

    try {
      return (_.reduce(values, (result, group, key) => {
        result.push((
          <AgeGroupItem
            key={`age-group-${inputData.slug}-${key}`}
            name={group.key}
            value={group.value}
            title={group.title}
            onChangeValue={this.onChangeValue}
            generateNewFormData={this.generateNewFormData}
          />
        ));

        return result;
      }, []));
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
  render() {
    const { inputData, errorString, extraInfo } = this.props;

    if (!inputData) {
      return null;
    }

    return (
      <FormInputWrapper
        errorString={errorString}
        className="age-group-margin"
        extraInfo={extraInfo}
      >
        <div className="age-group-container">
          {this.renderHeader()}
          <Row className="options-container">
            {this.renderAgeGroupItems()}
          </Row>
        </div>
      </FormInputWrapper>
    );
  }
}

AgeGroup.defaultProps = {
  value: null,
  errorString: null,
  extraInfo: null,
};


AgeGroup.propTypes = {
  value: PropTypes.array,
  inputData: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  errorString: PropTypes.string,
  extraInfo: PropTypes.object,
};

export default AgeGroup;
