import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import { dayTextList /* , deafultYearRange */ } from '../DatePicker.constants';

import './DaysList.scss';

/**
 * Custom Form Dropdown class
 */
class DaysList extends Component {
  /**
   * renderDaysText
   *
   * Render the days text
   */
  renderDaysText() {
    return _.map(dayTextList, day => (
      <Col
        xs="auto"
        className="days"
        key={day}
      >
        <p>{day}</p>
      </Col>
    ));
  }

  render() {
    return (
      <Row className="days-list-container" noGutters>
        <Col className="days-list-inner" xs={12}>
          <Row className="w-100" noGutters>
            {this.renderDaysText()}
          </Row>
        </Col>
        <Col className="days-list-divider" xs={12} />
      </Row>
    );
  }
}

export default DaysList;
