/**
 * DatePickerV2.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { Input } from 'reactstrap';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import moment from 'moment';

import Arrow from 'assets/images/forms/arrow.svg';
import DateIn from 'assets/images/forms/dateIn.svg';
import DateOut from 'assets/images/forms/dateOut.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';
import DateInner from './DateInner/DateInner';

import { deafultYearRange } from './DatePicker.constants';

import {
  isDateEqual,
  // storeStringToDate,
  slashedStringDateToDate,
  dateToString,
  // dateToStoreString,
} from '../../../lib/dateFunctions';

import '../DropDown/DropDown.scss';
import './DatePicker.scss';


/**
 * Custom Form Dropdown class
 */
class DatePicker extends Component {
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // size - Size of this control
    // startDate - Start range of date
    // endDate - End range of date
    // currDisplayDate - Day to set current view in calender (Default to today)
    const {
      startDate,
      endDate,
      startValue,
    } = this.props;

    // Get current date
    const date = startValue || new Date();
    const year = date.getFullYear();
    const month = date.getMonth();
    // Get first and last day of the month to display
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    // Get the calender range
    const dateRangeStart = startDate != null ? startDate : new Date(year - deafultYearRange, month);
    const dateRangeEnd = endDate != null ? endDate : new Date(year + deafultYearRange, month);

    // isOpened - Whether dropdown is opened
    // isError - Whether there is input error
    // selectedString - The selected date string
    // currDisplayDate - Current selected date in view
    // currFirstDay - Current first day of the month in view
    // currLastDay - Current last day of the month in view
    // monthDropdownOpen - Whether month selector is opened
    // yearDropdownOpen - Whether year selector is opened
    // dateRangeStart - The actual calender start range
    // dateRangeEnd - The actual calender end range
    this.state = {
      isOpened: false,
      currDisplayDate: date,
      currFirstDay: firstDay,
      currLastDay: lastDay,
      dateRangeStart,
      dateRangeEnd,
      isArrowUp: false,
      startDateString: '',
      endDateString: '',
      isSelectingStartDate: true,
      inputWidth: 0,
      isResp: false,
    };

    this.handleSelectClick = this.handleSelectClick.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);

    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleExit = this.handleExit.bind(this);

    this.setSelectedDate = this.setSelectedDate.bind(this);
    this.setNewDisplayDate = this.setNewDisplayDate.bind(this);
    this.onMovement = this.onMovement.bind(this);

    this.renderInputs = this.renderInputs.bind(this);
    this.renderInner = this.renderInner.bind(this);

    this.dateValidateAndUpdate = this.dateValidateAndUpdate.bind(this);
  }

  /**
   * Lifecycle Method - componentDidMount
   */
  componentDidMount() {
    // Add Handler to handle click outside of screen
    document.addEventListener('mousedown', this.handleClickOutside);
    window.addEventListener('scroll', this.onMovement);
    window.addEventListener('resize', this.onMovement);

    const {
      startValue,
      endValue,
    } = this.props;

    if (startValue) {
      this.setSelectedDate(startValue, true);
    }

    if (endValue) {
      this.setSelectedDate(endValue < startValue ? startValue : endValue, false);
    }

    this.setState({
      dateInnerDOM: ReactDOM.findDOMNode(this.dateInner),
      datePickerDOM: ReactDOM.findDOMNode(this.wrapperRef),
    });

    this.onMovement();
  }

  /**
   * Lifecycle Method - componentDidUpdate
   *
   * Used to update dropdown position
   * of month and year selector
   */
  async componentDidUpdate(prevProps) {
    const {
      startValue,
      endValue,
    } = this.props;

    if (
      ((prevProps.startValue || startValue) && !isDateEqual(prevProps.startValue, startValue)) ||
      ((prevProps.endValue || endValue) && !isDateEqual(prevProps.endValue, endValue))
    ) {
      const updateData = this.dateValidateAndUpdate();

      this.setState({
        startDateString: updateData.startDateString === 'Invalid Date' ? '' : updateData.startDateString,
        endDateString: updateData.endDateString === 'Invalid Date' ? '' : updateData.endDateString,
      });
    }
  }

  /**
   * Lifecycle Method - componentWillUnmount
   */
  componentWillUnmount() {
    // Remove Handler to handle click outside of screen
    document.removeEventListener('mousedown', this.handleClickOutside);
    window.removeEventListener('scroll', this.onMovement);
    window.removeEventListener('resize', this.onMovement);
  }


  // Make sure that the popup is always in the window
  onMovement() {
    const {
      dateInnerDOM,
      datePickerDOM,
      isOpened,
      isResp,
    } = this.state;

    const { innerWidth } = window;

    if (isOpened) {
      const newState = {};
      let clientRect = null;
      const currIsResp = innerWidth < 576;

      if (dateInnerDOM && datePickerDOM) {
        let top = 56;
        let left = 0;
        let right = 0;
        if (!currIsResp) {
          clientRect = datePickerDOM.getBoundingClientRect();
          const isOverHeight = !currIsResp && clientRect.bottom + 540 > window.outerHeight;
          newState.isArrowUp = !isOverHeight;

          const isOverFlow = clientRect.left + 530 > window.innerWidth;
          left = isOverFlow ? `${window.innerWidth - 530}px` : `${clientRect.left}px`;
          top = !currIsResp && isOverHeight ? clientRect.top - 330 : clientRect.bottom + 10;
          right = '';
        }

        dateInnerDOM.style.left = left;
        dateInnerDOM.style.right = right;
        dateInnerDOM.style.top = `${top}px`;
        newState.inputWidth = this.wrapperRef.getBoundingClientRect().width;
      }

      if ((isResp && !currIsResp) || (!isResp && currIsResp)) {
        newState.isResp = currIsResp;
      }

      this.setState(newState);
    }
  }

  /**
   * setNewDisplayDate
   *
   * Set new current date and
   * start and end of calender
   *
   * @param {date} date
   */
  setNewDisplayDate(date) {
    const dateToUse = date || new Date();
    const newYear = dateToUse.getFullYear();
    const newMonth = dateToUse.getMonth();

    // Get first and last day of visible month
    const firstDay = new Date(newYear, newMonth, 1);
    const lastDay = new Date(newYear, newMonth + 1, 0);

    // Set new calender values
    this.setState({
      currDisplayDate: dateToUse,
      currFirstDay: firstDay,
      currLastDay: lastDay,
    });
  }

  /**
   * setSelectedDate
   *
   * Updated selected date to store
   *
   * @param {date} date
   */
  async setSelectedDate(date, isStart = true) {
    return new Promise(async (resolve) => {
      try {
        const updateData = this.dateValidateAndUpdate(isStart && date, !isStart && date);

        if (updateData) {
          if (isStart && updateData.currStartValue) {
            this.setNewDisplayDate(updateData.currStartValue);
          }
        }

        this.setState({
          isOpened: !!(updateData && updateData.isChooseEndDate),
          isSelectingStartDate: false,
          startDateString: updateData.startDateString,
          endDateString: updateData.endDateString,
        }, resolve);
      } catch (error) {
        // eslint-disable-next-line
        resolve();
      }
    });
  }

  getDateFromDateString(dateString) {
    try {
      let date = slashedStringDateToDate(dateString);
      if (date && date.toString() === 'Invalid Date') {
        date = moment(dateString).toDate();
      }

      return date;
    } catch (error) {
      // eslint-disable-next-line
    }

    return 'Invalid Date';
  }

  /**
   * End selection if click on outside
   *
   * @param {object} event
   */
  handleClickOutside(event) {
    const { isOpened, dateInnerDOM } = this.state;

    // If clicked outside of daterpicekr Inner or wrapper, go to exit
    if (
      isOpened &&
      this.wrapperRef &&
      !this.wrapperRef.contains(event.target) &&
      dateInnerDOM &&
      !dateInnerDOM.contains(event.target)
    ) {
      this.setState({
        isOpened: false,
      });

      this.handleExit();
    }
  }

  /**
   * handleKeyPress
   *
   * Handle on enter key
   *
   * @param {object} target
   */
  handleKeyPress(target) {
    // If pressed on enter, go to exit
    if (target.charCode === 13) {
      target.preventDefault();
      this.handleExit();
    }
  }

  /**
   * handleSelectClick
   *
   * Handle toggling of datepicker
   */
  handleSelectClick(isStart = true) {
    const {
      isOpened,
      isSelectingStartDate,
    } = this.state;

    const {
      startValue,
      endValue,
      isRange,
    } = this.props;

    let openStartDateString = {};
    let openEndDateString = {};

    if (!isOpened) {
      openStartDateString = { startDateString: startValue && dateToString(startValue) };
      openEndDateString = { endDateString: endValue && dateToString(endValue) };
    }

    this.setState({
      isOpened: ((isSelectingStartDate && !isStart) || (!isSelectingStartDate && isStart) || !isOpened),
      isSelectingStartDate: isStart,
      ...openStartDateString,
      ...openEndDateString,
    }, () => {
      if (this.state.isOpened) {
        this.onMovement();
        this.setNewDisplayDate(this.state.isSelectingStartDate ? startValue : endValue);
        return;
      }

      const {
        startDateString: closeStartDateString,
        endDateString: closeEndDateString,
      } = this.state;

      const startDate = this.getDateFromDateString(closeStartDateString);
      const endDate = isRange && this.getDateFromDateString(closeEndDateString);

      const updateData = this.dateValidateAndUpdate(startDate, endDate);

      this.setState({
        startDateString: updateData.startDateString,
        endDateString: updateData.endDateString,
      });
    });
  }

  /**
   * handleExit
   *
   * Handle exit selection of entered input
   */
  handleExit(newDate, isStart) {
    const {
      startDateString,
      endDateString,
    } = this.state;

    const {
      endName,
    } = this.props;

    let startDate = isStart && newDate;
    let endDate = !isStart && endName && newDate;

    try {
      if (!startDate) {
        startDate = this.getDateFromDateString(startDateString);
      }
      if (endName && !endDate) {
        endDate = this.getDateFromDateString(endDateString);
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    const updateData = this.dateValidateAndUpdate(startDate, endDate);

    if (updateData) {
      this.setState({
        isOpened: false,
        isSelectingStartDate: false,
        startDateString: updateData.startDateString,
        endDateString: updateData.endDateString,
      });
    }
  }

  dateValidateAndUpdate(newStartValue = null, newEndValue = null) {
    const { dateRangeStart, dateRangeEnd } = this.state;
    const {
      startValue,
      endValue,
      offsetDays,
      isRange,
      onChange,
      name,
      endName,
    } = this.props;

    // Whether need user to choose new end date
    let isChooseEndDate = false;

    // Check if inputs are invalid dates
    const isNewStartInvalid = newStartValue && newStartValue.toString() === 'Invalid Date';
    const isNewEndInvalid = newEndValue && newEndValue.toString() === 'Invalid Date';

    // Get new or existing dates
    const startValueToUse = isNewStartInvalid ? null : ((!isNewStartInvalid && newStartValue) || startValue);
    const endValueToUse = isNewEndInvalid ? null : ((!isNewEndInvalid && newEndValue) || endValue);

    // Dates to be modified later
    let currStartValue = startValueToUse;
    let currEndValue = endValueToUse;

    // The date string for the input field
    let startDateString = 'Invalid Date';
    let endDateString = 'Invalid Date';

    try {
      // Choose end date if is range picker and have new start but no end value
      isChooseEndDate = isRange && newStartValue && !endValue;

      // For range only
      if (isRange) {
        // The max date is end range + 181 days
        const trueMaxEnd = dateRangeEnd && moment(dateRangeEnd).add(offsetDays, 'days').toDate();
        // Max curr date is current start date + 181
        const currMaxEnd = startValueToUse && moment(startValueToUse).add(offsetDays, 'days').toDate();
        // Get the smallest end date
        const maxEnd = currMaxEnd < trueMaxEnd ? currMaxEnd : trueMaxEnd;

        // If selected new start date is greater than end date,
        // make end date to be 1 day after new start date
        // and let user choose end date again
        if (newStartValue && startValueToUse > endValueToUse) {
          currEndValue = moment(startValueToUse).add(1, 'days').toDate();
          isChooseEndDate = true;
        }

        // If selected new end date is less than start date,
        // swap both dates
        // and let user choose end date again
        if (newEndValue && startValueToUse > endValueToUse) {
          currStartValue = endValueToUse;
          currEndValue = startValueToUse;
          isChooseEndDate = true;
        }

        // If end date is less than start range, make it to start range
        if (currEndValue && currEndValue < dateRangeStart) {
          currEndValue = dateRangeStart;
        }

        // If start date is greater than end range, make it to end range
        if (currStartValue && currStartValue > maxEnd) {
          currStartValue = maxEnd;
        }

        // If end date is more than end range, make it to end range
        if (currEndValue && currEndValue > maxEnd) {
          currEndValue = maxEnd;
        }

        // if new end date is diff from state, update it
        if (!isDateEqual(endValue, currEndValue)) {
          onChange('date', endName, currEndValue);
        }
      }

      // If start date is less than start range, make it to start range
      if (currStartValue && currStartValue < dateRangeStart) {
        currStartValue = dateRangeStart;
      }

      // If start date is greater than end range, make it to end range
      // No need max end as start date need to be within end range
      if (currStartValue && currStartValue > dateRangeEnd) {
        currStartValue = dateRangeEnd;
      }

      // if new start date is diff from state, update it
      if (!isDateEqual(startValue, currStartValue)) {
        onChange('date', name, currStartValue);
      }

      // Get new input strings
      startDateString = currStartValue ? moment(currStartValue).format('DD MMM YYYY') : startDateString;
      endDateString = currEndValue ? moment(currEndValue).format('DD MMM YYYY') : startDateString;
    } catch (error) {
      // eslint-disable-next-line
    }

    return {
      isChooseEndDate,
      currEndValue,
      currStartValue,
      startDateString,
      endDateString,
    };
  }

  renderDropDownArrow() {
    const { isOpened } = this.state;

    return (
      <button
        type="button"
        className="selected-arrow"
        onMouseDown={this.handleSelectClick}
      >
        <img
          src={Arrow}
          alt="arrow.svg"
          className={`selected-arrow-style ${isOpened ? 'angle-up' : 'angle-down'}`}
        />
      </button>
    );
  }

  renderInputField(isStart) {
    const {
      startDateString,
      endDateString,
      isSelectingStartDate,
      isOpened,
    } = this.state;

    const {
      name,
      endName,
      isDisableTyping,
      placeholderString,
      noInternalDesc,
      isRange,
    } = this.props;

    const rangeStyle = isRange ? 'range-style' : '';
    let selectingDateStyle = '';
    if (isOpened && isRange) {
      selectingDateStyle = (isSelectingStartDate && isStart) || (!isSelectingStartDate && !isStart) ? 'faded-style' : '';
    }

    const displayValue = isStart ? startDateString : endDateString;
    const nameToUse = isStart ? name : endName;

    return (
      <button
        type="button"
        className={`selected-text text-style ${rangeStyle} ${selectingDateStyle}`}
        onKeyPress={this.handleKeyPress}
        onMouseDown={(e) => {
          if (window.innerWidth <= 576) {
            e.preventDefault();
          }
          this.handleSelectClick(isStart);
        }}
      >
        <Input
          value={displayValue || ''}
          name={nameToUse}
          onChange={e => this.setState({
            [isStart ? 'startDateString' : 'endDateString']: e.target.value,
          })}
          placeholder={noInternalDesc ? placeholderString : ''}
          autoComplete="off"
          disabled={isDisableTyping}
        />
      </button>
    );
  }

  renderInputs() {
    const {
      isRange,
    } = this.props;

    if (!isRange) {
      return (
        <Fragment>
          {this.renderInputField(true)}
          {this.renderDropDownArrow()}
        </Fragment>
      );
    }

    return (
      <Fragment>
        <div className="date-input-container">
          <div className="img-container">
            <img
              className="date-in"
              src={DateIn}
              alt="Date Range Icon"
            />
          </div>
          {this.renderInputField(true)}
        </div>
        <div className="seperator" />
        <div className="date-input-container">
          <div className="img-container">
            <img
              className="date-out"
              src={DateOut}
              alt="Date Range Icon"
            />
          </div>
          {this.renderInputField(false)}
        </div>
        {this.renderDropDownArrow()}
      </Fragment>
    );
  }

  renderPopup() {
    const {
      isOpened,
      currDisplayDate,
      currFirstDay,
      currLastDay,
      dateRangeStart,
      dateRangeEnd,
      isArrowUp,
      isSelectingStartDate,
      inputWidth,
      isResp,
    } = this.state;

    const {
      isRange,
      startValue,
      endValue,
      offsetDays,
    } = this.props;


    return (
      <DateInner
        ref={node => this.dateInner = node}
        isOpened={!!isOpened}
        isRange={isRange}
        currDisplayDate={currDisplayDate}
        currFirstDay={currFirstDay}
        currLastDay={currLastDay}
        startValue={startValue}
        endValue={endValue}
        dateRangeStart={dateRangeStart}
        dateRangeEnd={dateRangeEnd}
        setSelectedDate={this.setSelectedDate}
        setNewDisplayDate={this.setNewDisplayDate}
        isArrowUp={isArrowUp}
        isSelectingStartDate={!!isSelectingStartDate}
        inputWidth={inputWidth}
        isResp={isResp}
        offsetDays={offsetDays}
      />
    );
  }

  /**
   * Render component
   */
  renderInner() {
    const { isOpened } = this.state;

    const {
      errorString,
      placeholderString,
      noInternalDesc,
      startValue,
    } = this.props;

    const dropDownOpenStyle = isOpened ? 'dropdown-container-opened' : 'dropdown-container-closed';
    const isErrorStyle = errorString ? 'error' : '';
    const titleStyle = startValue ? 'focused-title' : '';
    const noInternalDescStyle = noInternalDesc ? 'no-internal-desc' : '';

    return (
      <div
        className={`dropdown-container-v3 datepicker-container-v3 ${noInternalDescStyle}`}
        ref={node => (this.wrapperRef = node)}
      >
        <div className={`dropdown-container ${dropDownOpenStyle}  ${isErrorStyle}`}>
          {
            noInternalDesc ? null :
            <div className={`title-header ${titleStyle}`}>
              {placeholderString}
            </div>
          }
          <div className="selected-container align-items-center">
            {this.renderInputs()}
          </div>
        </div>
        {this.renderPopup()}
      </div>
    );
  }

  /**
   * Render component
   */
  render() {
    const {
      isRange,
      desc,
      extraInfo,
      errorString,
    } = this.props;

    // Range picker will take over the wrapper
    if (!isRange) {
      return (
        <FormInputWrapper
          desc={desc}
          extraInfo={extraInfo}
          errorString={errorString}
        >
          {this.renderInner()}
        </FormInputWrapper>
      );
    }

    return this.renderInner();
  }
}

DatePicker.defaultProps = {
  isRange: false,
  startDate: null,
  endDate: null,
  desc: null,
  extraInfo: null,
  errorString: null,
  noInternalDesc: false,
  isDisableTyping: false,
  endName: null,
  placeholderString: null,
  startValue: null,
  endValue: null,
  offsetDays: 0,
};

DatePicker.propTypes = {
  name: PropTypes.string.isRequired,
  endName: PropTypes.string,
  isRange: PropTypes.bool,
  startDate: PropTypes.instanceOf(Date),
  endDate: PropTypes.instanceOf(Date),
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  isDisableTyping: PropTypes.bool,
  noInternalDesc: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  placeholderString: PropTypes.string,
  startValue: PropTypes.instanceOf(Date),
  endValue: PropTypes.instanceOf(Date),
  offsetDays: PropTypes.number,
};

export default DatePicker;
