/**
 * DateInner.js
 * DateView only for rendering
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */

import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';

import './Calender.scss';

/**
 * Calender
 *
 * Render calender days for range selection
 */
class Calender extends Component {
  render() {
    const {
      dateRangeEndOverwrite,
      isSelectingStartDate,
      startValue,
      endValue,
      dateRangeStart,
      dateRangeEnd,
      setSelectedDate,
      isRange,
      currDisplayDate,
      hoverDate,
      setDateRangeEndOverwrite,
      setHoverDate,
      offsetDays,
    } = this.props;

    const currFirstDay = new Date(currDisplayDate);
    currFirstDay.setDate(1);

    const currLastDay = new Date(currFirstDay.getFullYear(), currFirstDay.getMonth() + 1, 0);

    const currFirstDayTime = currFirstDay.getTime();
    const currLastDayTime = currLastDay.getTime();

    const month = currFirstDay.getMonth();
    const year = currFirstDay.getFullYear();

    // Get the starting blank days of the calender
    const startIndex = currFirstDay.getDay();
    // Get the end day
    const endIndex = currLastDay.getDate();

    // Get starting time
    const currHoverTime = hoverDate && hoverDate.getTime();
    const startTime = startValue && startValue.getTime();
    const endTime = endValue && endValue.getTime();

    let startTimeToUse = startTime;
    let endTimetoUse = endTime;

    const fixedDateTime = isSelectingStartDate ? endTime : startTime;
    const nonfixedDateTime = !isSelectingStartDate ? endTime : startTime;

    if (hoverDate) {
      // eslint-disable-next-line
      startTimeToUse = currHoverTime < fixedDateTime ? currHoverTime : fixedDateTime;
      // eslint-disable-next-line
      endTimetoUse = currHoverTime > fixedDateTime ? currHoverTime : fixedDateTime;
    }

    const result = [];

    // These are blank elements to simulate last month dates
    for (let i = 0; i < startIndex; ++i) {
      result.push(<div className="date-col" key={`blank${i.toString()}`} />);
    }

    for (let i = 1; i <= endIndex; ++i) {
      const currDay = new Date(year, month, i);
      const currDayOfWeek = currDay.getDay();
      const currTime = currDay.getTime();

      const isSat = currDayOfWeek === 6;
      const isSun = currDayOfWeek === 0;
      const isStartDate = startTimeToUse === currTime;
      const isEndDate = endTimetoUse === currTime;
      const isWithin = startTimeToUse && endTimetoUse && startTimeToUse < currTime && currTime < endTimetoUse;
      const isFirstDay = currFirstDayTime === currTime;
      const isLastDay = currLastDayTime === currTime;

      const isSameDay = startTimeToUse === endTimetoUse || (hoverDate && fixedDateTime === currHoverTime);
      const isLeftBlockActive = !isSameDay && (!isSun && !isFirstDay) && ((isEndDate && startTimeToUse)
                                || isWithin);
      const isRightBlockActive = !isSameDay && (!isSat && !isLastDay) && ((isStartDate && endTimetoUse)
                                || isWithin);
      const isDayActive = isStartDate || isEndDate;
      const isEndActive = isDayActive || (isWithin && (isSat || isSun || isFirstDay || isLastDay));

      // Left bock, right block, circle block and selected circle block
      const leftBlockStyle = isLeftBlockActive ? 'left-active' : 'left-inactive';
      const rightBlockStyle = isRightBlockActive ? 'right-active' : 'right-inactive';
      const endBlockStyle = isEndActive ? 'end-active' : 'end-inactive';
      const dayStyle = isDayActive ? 'day-active' : 'day-inactive';
      const focusStyle = (currHoverTime && currHoverTime === currTime) || (!currHoverTime && nonfixedDateTime === currTime) ? 'day-focus' : '';

      // Check if day is in within selected range
      let isBlocked = false;
      // if (isRange) {
      //   if (isSelectingStartDate && endValue) {
      //     // isBlocked = currDay > endValue;
      //   } else if (!isSelectingStartDate && startValue) {
      //     // isBlocked = currDay < startValue;
      //   }
      // }

      // Check if day is out of calender range
      if (!isBlocked) {
        isBlocked = currDay < dateRangeStart || currDay > (dateRangeEndOverwrite || dateRangeEnd);
      }

      result.push((
        <Col
          key={`day${i.toString()}`}
          className="date-col"
          xs="auto"
        >
          <button
            className="day-container"
            type="button"
            onClick={() => {
              if (!isBlocked) {
                const { isSelectingStartDate: isSelectingStartDateCurr } = this.props;
                setSelectedDate(new Date(year, month, i), isSelectingStartDateCurr);
                setHoverDate(null);

                if (isRange && isSelectingStartDateCurr) {
                  setDateRangeEndOverwrite(new Date(year, month, i + offsetDays));
                }
              }
            }}
            onMouseEnter={() => {
                if (!isBlocked) {
                  setHoverDate(new Date(year, month, i));
                }
            }}
            onMouseLeave={() => {
                // if (!isSelectingStartDate) {
                  setHoverDate(null);
                // }
            }}
          >
            <div className={`day-range  ${leftBlockStyle}`} />
            <div className={`day-range  ${rightBlockStyle}`} />
            <div className={`${endBlockStyle}`} />
            {
              isBlocked ?
                <div className="day-disabled">
                  <p className="blocked">{i}</p>
                </div>
              :
                <div className={`day ${dayStyle} ${focusStyle}`}>
                  <p>{i}</p>
                </div>
            }
          </button>
        </Col>
      ));
    }

    return (
      <Row className="calender-container" noGutters>
        {result}
      </Row>
    );
  }
}

Calender.defaultProps = {
  startValue: null,
  endValue: null,
  dateRangeEndOverwrite: null,
  currDisplayDate: null,
  hoverDate: null,
  offsetDays: 0,
};

Calender.propTypes = {
  isRange: PropTypes.bool.isRequired,
  startValue: PropTypes.instanceOf(Date),
  endValue: PropTypes.instanceOf(Date),
  dateRangeStart: PropTypes.instanceOf(Date).isRequired,
  dateRangeEnd: PropTypes.instanceOf(Date).isRequired,
  dateRangeEndOverwrite: PropTypes.instanceOf(Date),
  currDisplayDate: PropTypes.instanceOf(Date),
  hoverDate: PropTypes.instanceOf(Date),
  setSelectedDate: PropTypes.func.isRequired,
  isSelectingStartDate: PropTypes.bool.isRequired,
  setDateRangeEndOverwrite: PropTypes.func.isRequired,
  setHoverDate: PropTypes.func.isRequired,
  offsetDays: PropTypes.number,
};

export default Calender;
