/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './Selection.scss';

/**
 * Custom Form Dropdown class
 */
class Selection extends Component {
  render() {
    const {
      name,
      desc,
      extraInfo,
      errorString,
      options,
      onChange,
      value,
      size,
    } = this.props;

    return (
      <FormInputWrapper
        desc={desc}
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div className="position-relative">
          <Row
            className="selection-container-v3 w-100"
            noGutters
          >
            {
              _.map(options, (option, index) => {
                const selectedStyle = value === option.value ? 'selected' : '';

                return (
                  <Col
                    xs={12}
                    sm={size}
                    key={`${option.value}-${index}`}
                  >
                    <button
                      type="button"
                      className={selectedStyle}
                      onClick={() => onChange('selection', name, option.value)}
                    >
                      {option.display_name}
                    </button>
                  </Col>
                );
              })
            }
          </Row>
        </div>
      </FormInputWrapper>
    );
  }
}

Selection.defaultProps = {
  value: null,
  desc: null,
  extraInfo: null,
  errorString: null,
  size: 6,
};


Selection.propTypes = {
  value: PropTypes.string,
  name: PropTypes.string.isRequired,
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  options: PropTypes.array.isRequired,
  size: PropTypes.number,
};

export default Selection;
