/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Row, Col, Input } from 'reactstrap';
import PropTypes from 'prop-types';

import './AgeGroupItem.scss';

/**
 * Custom Form Dropdown class
 */
class AgeGroupItem extends Component {
  constructor(props) {
    super(props);

    this.handleExit = this.handleExit.bind(this);
  }

  handleExit(e, isBlur) {
    const { generateNewFormData } = this.props;

    if (isBlur || (!isBlur && e.charCode === 13)) {
      generateNewFormData();
    }
  }

  render() {
    const {
      name,
      value,
      title,
      onChangeValue,
    } = this.props;

    if (!name) {
      return null;
    }

    return (
      <Col className="mb-3" xs={12} md={6} lg={6}>
        <div className="age-group-item-container">
          <p className="title">{title}</p>
          <Row className="selector" noGutters>
            <Col className="d-flex justify-content-center align-items-center" xs={12}>
              <Input
                className="age-group-input"
                type="number"
                value={value || ''}
                onChange={e => onChangeValue(name, e.target.value && Math.max(0, Math.floor(e.target.value)))}
                onBlur={e => this.handleExit(e, true)}
                onKeyPress={e => this.handleExit(e, false)}
                placeholder="Enter number"
              />
            </Col>
          </Row>
        </div>
      </Col>
    );
  }
}

AgeGroupItem.defaultProps = {
  value: null,
};


AgeGroupItem.propTypes = {
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  name: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  onChangeValue: PropTypes.func.isRequired,
  generateNewFormData: PropTypes.func.isRequired,
};

export default AgeGroupItem;
