/**
 * DateInner.js
 * DateView only for rendering
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import { Scrollbars } from 'react-custom-scrollbars';

import { monthTextMapping } from '../DatePicker.constants';

import './MonthYearSelector.scss';

// Height for month and year dropdown items
const itemHeight = 32;

/**
 * Custom Form Dropdown class
 */
class MonthYearSelector extends Component {
  constructor(props) {
    super(props);

    this.updateScrollPosition = this.updateScrollPosition.bind(this);
  }

  updateScrollPosition() {
    const {
      currDisplayDate,
      dateRangeStart,
      isYear,
    } = this.props;

    try {
      const height = itemHeight * (isYear ? currDisplayDate.getFullYear() - dateRangeStart.getFullYear() : currDisplayDate.getMonth());

      // Update year scroll position to current year
      if (this.scrollInput) {
        setTimeout(() => this.scrollInput.scrollTop(height), 10);
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  /**
   * renderMonths
   *
   * Render months dropdown
   */
  renderMonths() {
    const {
      currDisplayDate,
      dateRangeStart,
      dateRangeEnd,
      setCalender,
    } = this.props;

    try {
      const startYear = dateRangeStart.getFullYear();
      const endYear = dateRangeEnd.getFullYear();
      const currYear = currDisplayDate.getFullYear();

      const startMonth = currYear === startYear ? dateRangeStart.getMonth() : 0;
      const endMonth = currYear === endYear ? dateRangeEnd.getMonth() : 11;

      return _.reduce(monthTextMapping, (result, item, index) => {
        if (startMonth <= index && index <= endMonth) {
          const jsx = (
            <DropdownItem
              onClick={() => {
                setCalender(index, currYear);
              }}
              key={`month-${index.toString()}`}
            >
              {item}
            </DropdownItem>
          );
          result.push(jsx);
        }

        return result;
      }, []);
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  /**
   * renderYears
   *
   * Render years dropdown
   */
  renderYears() {
    const {
      currDisplayDate,
      dateRangeStart,
      dateRangeEnd,
      setCalender,
    } = this.props;

    try {
      const start = dateRangeStart.getFullYear();
      const end = dateRangeEnd.getFullYear();

      const result = [];
      for (let i = start; i <= end; ++i) {
        result.push((
          <DropdownItem
            onClick={() => {
              setCalender(currDisplayDate.getMonth(), i);
            }}
            key={`year-${i.toString()}`}
          >
            {i}
          </DropdownItem>
        ));
      }

      return result;
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    const { isYear, currDisplayDate, isSingle } = this.props;

    try {
      const dateToUse = new Date(currDisplayDate.getFullYear(), currDisplayDate.getMonth(), 1);

      let displayText = '';

      if (isYear) {
        displayText = dateToUse.getFullYear();
      } else {
        const month = dateToUse.getMonth();
        displayText = monthTextMapping[month];
      }

      if (!isSingle) {
        return <p>{displayText}</p>;
      }

      return (
        <div className="month-year-selector">
          <UncontrolledDropdown>
            <div ref={(dateMonthInput) => { this.dateMonthInput = dateMonthInput; }}>
              <DropdownToggle
                caret
                outline
                onClick={this.updateScrollPosition}
              >
                {displayText}
              </DropdownToggle>
            </div>
            <DropdownMenu>
              <Scrollbars
                autoHeight
                ref={(scrollInput) => { this.scrollInput = scrollInput; }}
              >
                {isYear ? this.renderYears() : this.renderMonths()}
              </Scrollbars>
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

MonthYearSelector.defaultProps = {
  isYear: false,
  isSingle: false,
};

MonthYearSelector.propTypes = {
  isYear: PropTypes.bool,
  setCalender: PropTypes.func.isRequired,
  currDisplayDate: PropTypes.instanceOf(Date).isRequired,
  dateRangeStart: PropTypes.instanceOf(Date).isRequired,
  dateRangeEnd: PropTypes.instanceOf(Date).isRequired,
  isSingle: PropTypes.bool,
};

export default MonthYearSelector;
